@charset "utf-8";

////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Import partials

@import "dark-mode-toggle/dark-mode-toggle";




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Variables

$grid-breakpoints-xs:             0 !default;
$grid-breakpoints-sm:             576px !default;
$grid-breakpoints-md:             768px !default;
$grid-breakpoints-lg:             992px !default;
$grid-breakpoints-xl:             1200px !default;
$grid-breakpoints-xxl:            1400px !default;

$m-bg-color:                      #fff !default;
$m-bg-color-dark:                 #242424 !default;

$m-ani-dur-short:                 .6s !default;
$m-ani-dur-long:                  1s !default;
$m-ani-change-mode-dur:           1s !default;
$m-transition-timing-function:    cubic-bezier(.09, .68, 0, .99) !default;

$spacer:                          1rem !default;
$spacer-according-to-header:      2.2rem !default;

$font-family:                     'Roboto', Arial, sans-serif !default;
$font-family-heading:             'Roboto Condensed', Arial, sans-serif !default;




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Mixins

// .article {
//   @include scrollbars(10px, #ccc, #eee, #ccc, 10px, 3px, true);
// }
@mixin scrollbars($size, $foreground-color, $background-color, $resizer-color, $border-radius: 0, $thumb-border-width: 0, $customize-ie: false) {
  // For Google Chrome
  &::-webkit-scrollbar {
    width: $size;
    height: $size;
  }

  &::-webkit-scrollbar-thumb {
    border: $thumb-border-width solid rgba(0, 0, 0, 0);
    border-radius: $border-radius;
    background: $foreground-color;
    background-clip: padding-box;
  }

  &::-webkit-scrollbar-track {
    background: $background-color;
  }

  &::-webkit-resizer {
    background-color: $resizer-color;
  }

  // For IE
  @if $customize-ie {
    scrollbar-face-color: $foreground-color;
    scrollbar-track-color: $background-color;
    scrollbar-arrow-color: $foreground-color;
    scrollbar-shadow-color: $foreground-color;
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// General Settings

:root {
  --m-bg-color: #{$m-bg-color};
  --m-text-color: #333;
  --m-accent-color: #59a8e4;
  --m-neutral-color: rgba(156, 156, 156, 0.3);
}


*,
*:after,
*:before {
  box-sizing: border-box;
}

::selection {
  // color: #fff;
  text-shadow: none;
  background: var(--m-neutral-color);
}


html {
  font-size: 18px;
  // scroll-behavior: smooth;
}

body {
  background-color: var(--m-bg-color);
  color: var(--m-text-color);
  font-family: $font-family;
  font-size: 1rem;
  transition: background-color $m-ani-change-mode-dur;
  transition-timing-function: $m-transition-timing-function;
}


h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: $spacer * 2;
  margin-bottom: $spacer;

  color: var(--m-accent-color);
  font-family: $font-family-heading;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
}

h1 {
  margin-top: 0;
  padding-top: $spacer-according-to-header * 2;
  font-size: 3rem;

  @media (min-width: $grid-breakpoints-md) {
    font-size: 6rem;
  }
}


a {
  color: var(--m-accent-color);
  text-decoration: none;

  &:hover {
    color: var(--m-accent-color);
    text-decoration: underline;
  }
}


hr {
  margin-top: $spacer-according-to-header * 2.6;
  border-color: var(--m-neutral-color);
}


.m-mt {
  margin-top: $spacer * 2;
}
.m-mb {
  margin-top: $spacer;
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Dark Theme Settings

.m-theme-dark {
  --m-bg-color: #{$m-bg-color-dark};
  --m-text-color: #ccc;
  // --m-accent-color: #59a8e4;

  img {
    filter: brightness(.8) contrast(1.2);
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Notifier

$m-notifier-width-md: 400px !default;
$m-notifier-content-boxshadow-color: rgba(0, 0, 0, .3) !default;

.m-notifier {

  --m-notifier-content-bg-color:  #{darken($m-bg-color, 8%)};
  --m-notifier-btn-bg-color: #{$m-bg-color};

  position: fixed;
  bottom: 0;
  left: 0;
  margin-right: ($spacer * 1.2) * 2.6;
  max-width: 100%;
  font-size: .75rem;
  color: inherit;
  z-index: 2001;
  pointer-events: none;

  /// LG SCREEN
  @media (min-width: $grid-breakpoints-md) {
    max-width: $m-notifier-width-md;
  }
}

.m-notifier--fadein {
  pointer-events: auto;

  .m-notifier__content {
    transform: scale(1);
    opacity: 1;
  }
}

.m-notifier__content {
  margin: $spacer * 1.2;
  padding: $spacer;

  border-radius: 4px;
  background-color: var(--m-notifier-content-bg-color);
  box-shadow: 0 0 8px 3px $m-notifier-content-boxshadow-color;

  // backdrop-filter: blur(12px);
  // background-color: rgba(156, 156, 156, .25);
  // box-shadow: 0 0 8px 3px rgba(0, 0, 0, .2);

  transform: scale(.9);
  opacity: 0;
  transition: transform $m-ani-dur-short, opacity $m-ani-dur-short, background-color $m-ani-dur-short;
  transition-timing-function: $m-transition-timing-function;

  .badge {
    color: #fff;
    background-color: #000;
  }

  //////////////////////////////////////////////////////////////////////////////
  /// DARK MODE

  .m-theme-dark & {
    --m-notifier-content-bg-color: #{lighten($m-bg-color-dark, 8%)};
  }
}

.m-notifier__btn-holder {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-top: .5rem;

  > div {
    flex-grow: 1;
  }

  .m-n-btn {
    border: none;
    font-size: inherit;
    color: inherit;
    opacity: .6;
    background-color: var(--m-notifier-btn-bg-color);
    transition: background-color $m-ani-dur-short $m-transition-timing-function, opacity $m-ani-dur-short;

    &:hover,
    &:focus,
    &:active {
      opacity: 1;
    }

    &:focus {
      box-shadow: none;
    }

    //////////////////////////////////////////////////////////////////////////////
    /// DARK MODE

    .m-theme-dark & {
      --m-notifier-btn-bg-color: #{$m-bg-color-dark};
    }
  }

  .m-btn {
    font-size: inherit;
    &:focus {
      box-shadow: none !important;
    }
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Go up

.m-goup {
  position: fixed;
  bottom: 0;
  right: 0;

  margin: $spacer * 1.2;

  width: 2rem;
  height: 2rem;

  border-radius: 4px;
  background-color: rgba(99, 99, 99, 0.8);
  // backdrop-filter: blur(12px);
  // box-shadow: 0 0 8px 3px rgba(0, 0, 0, .2);

  transform: scale(.9);
  opacity: 0;
  transition: transform $m-ani-dur-short, opacity $m-ani-dur-short;
  transition-timing-function: $m-transition-timing-function;

  color: inherit;
  text-align: center;
  z-index: 2001;
  pointer-events: none;

  &:hover {
    transform: scale(1.1);
  }

  a {
    display: block;
    margin-top: .1rem;
    color: #fff;
  }

  .stretched-link:after {
    pointer-events: none;
  }
}

.m-goup--fadein {
  pointer-events: auto;
  transform: scale(1);
  opacity: 1;

  .stretched-link:after {
    pointer-events: auto;
  }
}



////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Header

$m-header-nav-social-h: 2.2rem !default;
$m-header-nav-elements-mobile-space: .8rem !default;
$m-header-nav-elements-space: .8rem !default;

.m-header {

  --m-header-bg-color: #333;
  --m-header-bg-color-2: #4e4e4e;
  --m-header-text-color: #fff;
  --m-header-link-color: #ccc;
  --m-header-link-active-color: var(--m-accent-color);

  padding: 0;
  color: var(--m-header-text-color);
  background-color: var(--m-header-bg-color);
  transition: background-color $m-ani-change-mode-dur;
  transition-timing-function: $m-transition-timing-function;
  box-shadow: 0 1px rgba(0, 0, 0, 0.2);

  .navbar-brand {
    color: var(--m-header-text-color);
    font-weight: bold;
  }

  .nav-link {
    color: var(--m-header-link-color);
    opacity: .5;
    transition: opacity $m-ani-dur-short, color $m-ani-dur-short;

    &.active,
    &:hover {
      opacity: 1;
    }

    &.active {
      color: var(--m-header-link-active-color);
    }
  }

  .navbar-brand,
  .nav-link {
    padding-left: .5rem;
    padding-right: .5rem;
    font-size: .8rem;
    text-transform: uppercase;
  }

  .navbar-toggler {
    padding: .1rem .4rem;

    .navbar-toggler-icon {
      background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28124, 124, 124, 0.8%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
    }
  }

  //////////////////////////////////////////////////////////////////////////////
  /// DARK MODE

  .m-theme-dark & {
    --m-header-bg-color: #fff;
    --m-header-bg-color-2: #f5f5f5;
    --m-header-text-color: #333;
    --m-header-link-color: #333;
    // --m-header-link-active-color: var(--m-accent-color);
  }
}


////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Header: Social

.m-header__nav-social {
  flex-direction: row !important;
  margin: $m-header-nav-elements-mobile-space 0;
  padding: 0 $m-header-nav-elements-space;
  height: $m-header-nav-social-h;
  background-color: var(--m-header-bg-color-2);
  transition: background-color $m-ani-change-mode-dur;
  transition-timing-function: $m-transition-timing-function;

  .nav-link {
    padding: .4rem .5rem .5rem;
  }

  /// LG SCREEN
  .navbar-expand-lg & {
    @media (min-width: $grid-breakpoints-lg) {
      margin: 0;
    }
  }
}


////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Header: Dark Mode Toggle

.m-header__nav-dark-mode-toggle {
  margin: $m-header-nav-elements-mobile-space 0;

  /// LG SCREEN
  .navbar-expand-lg & {
    @media (min-width: $grid-breakpoints-lg) {
      margin: 0;
    }
  }
}


.m-header__nav-dark-mode-toggle-holder {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-content: center;
  align-items: center;
}


.m-header__nav-dark-mode-toggle-text {
  padding: 0 10px;
  color: inherit;
  opacity: .5;

  p {
    margin: 0;
  }

  /// LG SCREEN
  .navbar-expand-lg & {
    @media (min-width: $grid-breakpoints-lg) {
      display: none;
    }
  }
}


.m-header__nav-dark-mode-toggle-toggler {
  line-height: 0;
}

////////////////////////////////////////////////////////////////////////////////
/// BLOCK CUSTOMIZATION: dark-mode-toggle

.ali-dark-mode-toggle {
  margin-left: calc( #{$m-header-nav-elements-space} * 1.5);
}

////////////////////////////////////////////////////////////////////////////////
/// VENDOR CUSTOMIZATION: Hamburgers

.hamburger-inner,
.hamburger-inner:after,
.hamburger-inner:before,
.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner:after,
.hamburger.is-active .hamburger-inner:before {
  background-color: var(--m-header-text-color);
}

.m-header .hamburger {
  padding: .35rem 0;
  line-height: 0;
  transform: scale(.7);
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Welcomer

$m-welcomer-top-bottom-space: $spacer-according-to-header !default; // According to header's height

$m-welcomer-img-width: 256px !default;
$m-welcomer-img-height: 256px !default;
$m-welcomer-img-width-sm: 512px !default;
$m-welcomer-img-height-sm: 512px !default;

$m-welcomer-txt-min-width: 17rem !default;
$m-welcomer-scene-min-width: 992px !default;
$m-welcomer-particles-height: 335px !default;

.m-welcomer {

  --m-welcomer-bg-color-up: #00d795;
  --m-welcomer-bg-color-down: #0d60d1;
  --m-welcomer-txt-color: #000;
  --m-welcomer-img-border-color: rgba(0, 0, 0, 0.8);
  --m-welcomer-txt-shadow-color: rgba(255, 255, 255, 0.5);
  --m-welcomer-particles-bg-pos: 0;
  --m-welcomer-img-bg-pos: 0;
  // --m-welcomer-particles-brightness: 1;

  position: relative;

  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
  align-items: center;

  padding: $m-welcomer-top-bottom-space 0;
  min-height: 100vh;
  background-image: var(--m-bg-color);
  background-image: linear-gradient(0deg, var(--m-welcomer-bg-color-down) 0%, var(--m-welcomer-bg-color-up) 100%);
  overflow: hidden;
  z-index: 1;

  // Gradient background smooth transition hack
  &:before {
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-color: #fff;
    z-index: -1;
    transition: opacity $m-ani-change-mode-dur;
    transition-timing-function: $m-transition-timing-function;
    opacity: .6;
  }


  //////////////////////////////////////////////////////////////////////////////
  /// DARK MODE
  .m-theme-dark &:before {
    opacity: 0;
  }

  .m-theme-dark & {
    --m-welcomer-txt-color: #fff;
    --m-welcomer-img-border-color: rgba(255, 255, 255, 0.8);
    --m-welcomer-txt-shadow-color: rgba(0, 0, 0, 0.5);
    // --m-welcomer-particles-brightness: 0;
  }
}


////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Welcomer: front & scene

.m-welcomer__front {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  align-content: center;

  margin: 1rem;
  width: 100%;
  z-index: 1;
}

.m-welcomer__scene {
  position: absolute;
  min-width: $m-welcomer-scene-min-width;
  width: 100%;
  z-index: -1;
}


////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Welcomer: description

.m-welcomer__description {
  position: relative;
  margin: 1rem;
  min-width: $m-welcomer-txt-min-width;
  color: var(--m-welcomer-txt-color);
  text-transform: uppercase;
  user-select: none;

  h1 {
    margin: 0;
    padding: 0;
    color: inherit;
    font-size: 2.5rem;
    font-weight: bold;
    line-height: 1;
    text-shadow: 0 1px 0 var(--m-welcomer-txt-shadow-color);
  }

  p {
    margin: 0;
    margin-top: -0.5rem;
    padding: 0;
    color: inherit;
    font-family: $font-family-heading;
    font-size: 1.04rem;
  }
}

.m-welcomer__description-hi {
  font-size: 1rem;
  line-height: .5;
}

.m-welcomer__description-im {
  margin-left: -.33rem;
  font-size: 6rem;
}

.m-welcomer__description-name {
  position: absolute;
  top: 3rem;
  left: 7rem;
}


////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Welcomer: image

@keyframes m-welcomer__image-ani-wait {
  from {
    opacity: 0;
    transform: scale(.8);
  }
  to {
    opacity: 0;
    transform: scale(.8);
  }
}

@keyframes m-welcomer__image-ani-go {
  from {
    opacity: 0;
    transform: scale(.8);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

.m-welcomer__image {
  margin: 1rem;
  width: $m-welcomer-img-width;
  height: $m-welcomer-img-height;
  overflow: hidden;
  filter: grayscale(0) brightness(1);
  transition: filter $m-ani-dur-short;
  animation: m-welcomer__image-ani-wait $m-ani-dur-long, m-welcomer__image-ani-go $m-ani-dur-short $m-ani-dur-long;
  animation-timing-function: cubic-bezier(0.34, 1.56, 0.64, 1);
  user-select: none;

  /// LG SCREEN
  @media (min-width: $grid-breakpoints-sm) {
    width: $m-welcomer-img-width-sm;
    height: $m-welcomer-img-height-sm;
  }

  &:hover {
    // transform: scale(1.02);
    filter: grayscale(.3) brightness(1.2);
  }

  img {
    width: 100%;
    height: auto;
    // border: 10px solid var(--m-welcomer-img-border-color);
    // border-radius: ($m-welcomer-img-width / 2);
    // clip-path: circle( ($m-welcomer-img-width / 2) at center );
    // transition: border-color $m-ani-dur-long;
    // transition-timing-function: $m-transition-timing-function;
  }
}

.m-welcomer__image-img {
  position: relative;
  width: 100%;
  height: 100%;
  background: url('../images/ali.webp') no-repeat;
  background-size: $m-welcomer-img-width;
  background-position: 0 calc( (#{$m-welcomer-img-height} * var(--m-welcomer-img-bg-pos)) * -1 );

  /// LG SCREEN
  @media (min-width: $grid-breakpoints-sm) {
    background-size: $m-welcomer-img-width-sm;
    background-position: 0 calc( (#{$m-welcomer-img-height-sm} * var(--m-welcomer-img-bg-pos)) * -1 );
  }
}


////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Welcomer: down button

@keyframes m-welcomer__down-btn-ani {
  from {
    transform: translate(0, 0);
  }
  to {
    transform: translateY(-10px);
  }
}

.m-welcomer__down-btn {
  position: absolute;
  bottom: 0;
  width: 100%;
  color: var(--m-welcomer-txt-color);
  font-size: 2rem;
  text-align: center;
  // text-shadow: 0 1px 0 var(--m-welcomer-txt-shadow-color);
  animation: m-welcomer__down-btn-ani $m-ani-dur-short 0s infinite alternate;

  a {
    display: block;
    color: inherit;
    transition: transform $m-ani-dur-short;

    &:hover {
      color: inherit;
      transform: scale(1.5);
    }
  }
}


////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Welcomer: particles

.m-welcomer__particles-wrapper {
  // It receive inline styles from the parallax.js lib.
  // NOTE: We give the wrapper 100% width, although it's parent which is
  // .m-welcomer__scene has the 100% width... Why? Because parallax.js lib sets
  // it's first child HTML tag's position to relative and the rest ones to
  // absolute! So If we don't set this here, then our the background image of
  // the other tags won't be shown.
  width: 100%;
}


.m-welcomer__particles-front,
.m-welcomer__particles-middle,
.m-welcomer__particles-back {
  height: $m-welcomer-particles-height;
  transform: scale(1.3);
  // filter: brightness( var(--m-welcomer-particles-brightness) );
  // transition: filter $m-ani-dur-long;
  // transition-timing-function: $m-transition-timing-function;

  &:before,
  &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: $m-welcomer-particles-height;
  }
  &:before {
    top: -#{$m-welcomer-particles-height};
    left: 0;
  }
  &:after {
    top: $m-welcomer-particles-height;
    left: 0;
  }
}


.m-welcomer__particles-front {
  background: url('../images/welcomer_particles-front.webp') repeat-x;

  &:before,
  &:after {
    background: url('../images/welcomer_particles-front.webp') repeat-x;
  }
}

.m-welcomer__particles-middle {
  background: url('../images/welcomer_particles-middle.webp') repeat-x;

  &:before,
  &:after {
    background: url('../images/welcomer_particles-middle.webp') repeat-x;
  }
}

.m-welcomer__particles-back {
  background: url('../images/welcomer_particles-back.webp') repeat-x;

  &:before,
  &:after {
    background: url('../images/welcomer_particles-back.webp') repeat-x;
  }
}


.m-welcomer__particles-front,
.m-welcomer__particles-middle,
.m-welcomer__particles-back {
  background-position: 0 calc( (#{$m-welcomer-particles-height} * var(--m-welcomer-particles-bg-pos)) * -1 );

  &:before,
  &:after {
    background-position: 0 calc( (#{$m-welcomer-particles-height} * var(--m-welcomer-particles-bg-pos)) * -1 );
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Container

.m-container-main {
  // Because our Header block gets around 35px height when 1rem = 16px. So we
  // give our container to have a top margin of 2.2rem.
  margin-top: $spacer-according-to-header;
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Sections

$m-section-bg-top: 100px !default;
$m-section-bg-left: -100px !default;
$m-section-bg-width: 300px !default;
$m-section-bg-height: 300px !default;

$m-section-bg-top-sm: 5% !default;
$m-section-bg-left-sm: -256px !default;
$m-section-bg-width-sm: 512px !default;
$m-section-bg-height-sm: 512px !default;

.m-section {
  position: relative;
}

.m-section-bg {
  position: absolute;
  top: $m-section-bg-top;
  left: $m-section-bg-left;
  width: $m-section-bg-width;
  height: $m-section-bg-height;

  transform: translateY( var(--m-section-bg-y) );
  transition: transform .1s linear;
  // transition-timing-function: $m-transition-timing-function;
  will-change: transform;

  background-size: cover;
  background-repeat: no-repeat;
  z-index: -1;


  /// LG SCREEN
  @media (min-width: $grid-breakpoints-sm) {
    top: $m-section-bg-top-sm;
    left: $m-section-bg-left-sm;
    width: $m-section-bg-width-sm;
    height: $m-section-bg-height-sm;
  }
}

.m-section-bg--about {
  background-image: url('../images/section-bg_about.webp');
  // background-position: 0 ( ($m-section-bg-height * 0) * -1 );
}
.m-section-bg--portfolio {
  background-image: url('../images/section-bg_portfolio.webp');
}
.m-section-bg--tutorials {
  background-image: url('../images/section-bg_tutorials.webp');
}
.m-section-bg--contact {
  background-image: url('../images/section-bg_contact.webp');
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Heading

.m-heading-animated {
  opacity: 0;
  transform: translate3d(0, -20%, 0);
  transition: opacity $m-ani-dur-long .2s, transform $m-ani-dur-long .2s;
  transition-timing-function: $m-transition-timing-function;
}

.m-heading-sub-animated {
  position: relative;
  padding: 0 1rem;
  opacity: 0;
  transform: translate3d(0, -20%, 0);
  transition: opacity $m-ani-dur-long .2s, transform $m-ani-dur-long .2s;
  transition-timing-function: $m-transition-timing-function;

  &:after {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 5px;
    height: 100%;
    border-radius: 2px;
    background-color: var(--m-accent-color);
  }
}

.m-heading-animated__shown,
.m-heading-sub-animated__shown {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}



////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Link

.m-link {
  border-bottom-style: solid;
  border-bottom-width: 2px;
  border-bottom-color: transparent;
  color: var(--m-accent-color);
  text-decoration: none;
  transition: border-color .3s;

  &:hover {
    border-bottom-color: var(--m-accent-color);
    color: var(--m-accent-color);
    text-decoration: none;
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Img

.m-img-holder {
  position: relative;
  margin: $spacer;
  transform-style: preserve-3d;

  &:hover {
    &:before {
      opacity: .6;
    }
    &:after {
      opacity: 1;
    }
  }

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: .25rem;

    background-color: var(--m-accent-color);
    transition: opacity $m-ani-dur-long;
    transition-timing-function: $m-transition-timing-function;
    opacity: 0;
    pointer-events: none;
    z-index: 1;
  }

  &:after {
    content: attr(title);
    position: absolute;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;

    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    padding: 0 40px;

    color: #fff;
    font-weight: bold;
    text-align: center;
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);

    transition: opacity $m-ani-dur-long;
    transition-timing-function: $m-transition-timing-function;
    opacity: 0;
    transform: translateZ(40px);
    pointer-events: none;
    z-index: 1;
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Card

.m-card {
  margin: 1rem;
  border: none;
  border-radius: 4px 4px 0 0;
  border-bottom: 2px solid;
  border-bottom-color: transparent;
  background-color: var(--m-neutral-color);
  transition: border-color $m-ani-dur-short;

  &:hover {
    border-bottom-color: var(--m-accent-color);
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Button

.m-btn {
  border: none;
  color: #fff;
  background-color: var(--m-accent-color);
  transition: background-color $m-ani-dur-short, filter $m-ani-dur-short, box-shadow .3s;

  // filter property causes the .stretched-link button a bug on hover. So I had
  // to mention two rules.
  &:not(.stretched-link):hover {
    color: #fff;
    background-color: var(--m-accent-color)!important;
    filter: brightness(1.15);
  }
  &.stretched-link:hover {
    color: #fff;
    background-color: var(--m-accent-color)!important;
    // pointer-events: none;
  }

  &:focus,
  &:active {
    background-color: var(--m-accent-color) !important;
  }

  &:focus {
    box-shadow: 0 0 0 0.2rem var(--m-neutral-color) !important;
  }
}


.m-btn-loader {
  position: relative;
  padding: $spacer / 1.33;

  &.m-btn-loader--loading {
    pointer-events: none;
  }
}

.m-btn-loader__txt,
.m-btn-loader__loading {
  transition: opacity $m-ani-dur-short;
  transition-timing-function: $m-transition-timing-function;
}

.m-btn-loader__txt {
  .m-btn-loader--loading & {
    opacity: 0;
  }
}

.m-btn-loader__loading {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: space-around;

  width: 100%;
  height: 100%;

  opacity: 0;

  svg {
    width: $spacer * 3;
  }

  svg path,
  svg rect,
  svg circle {
    fill: #fff;
  }

  .m-btn-loader--loading & {
    opacity: 1;
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Progress

.m-progress-holder {
  margin: 0 1rem;
}

.m-progress-holder.m-progress-holder__set {
  .progress-bar__graphicdesign {
    width: 95%;
  }
  .progress-bar__uiux {
    width: 95%;
  }
  .progress-bar__html {
    width: 90%;
  }
  .progress-bar__css {
    width: 90%;
  }
  .progress-bar__js {
    width: 85%;
  }
  .progress-bar__php {
    width: 70%;
  }
}

.m-progress {
  margin-bottom: 1rem;
  height: 30px;
  background-color: var(--m-neutral-color);

  .progress-bar {
    padding: 0 10px;
    width: 120px;
    background-color: var(--m-accent-color);
    text-align: left;
    transition: width $m-ani-dur-long;
    transition-timing-function: $m-transition-timing-function;
  }
}



////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// List

.m-list {
  position: relative;
  padding-left: 1rem;
  list-style: none; // Remove default bullets

  li:before {
  content: "\2022";
  display: inline-block;
  margin-left: -1rem;
  width: 1rem;
  color: var(--m-accent-color);
  font-size: 1.2rem;
  font-weight: bold;
}
}

.m-list-inline {
  display: inline-block;
  margin: 0 .3rem;
  padding: 0;
  list-style: none;
  vertical-align: text-bottom;

  li {
    display: inline;
    margin: 0 .3rem;
  }

  a {
    opacity: .6;
    transition: opacity $m-ani-dur-short;

    &:hover {
      opacity: 1;
    }
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Form

$m-form-error-color: #ea5353 !default;
$m-form-error-color-shadow: rgba(235, 83, 83, 0.3) !default;

.m-form {
  .form-control {
    color: inherit;
    border: 1px solid var(--m-neutral-color);
    background-color: transparent;
    // background-color: var(--m-neutral-color);

    @include scrollbars(.95rem, var(--m-neutral-color), rgba(156, 156, 156, 0), var(--m-bg-color), .95rem, .35rem, true);

    &:focus {
      color: inherit;
      background-color: transparent;
      box-shadow: 0 0 0 0.2rem var(--m-neutral-color);
      // background-color: var(--m-neutral-color);
      // box-shadow: 0 0 0 0.2rem rgba(156, 156, 156, 0.12);
    }
  }

  .form-control--error {
    border: 1px solid $m-form-error-color;

    &:focus {
      box-shadow: 0 0 0 0.2rem $m-form-error-color-shadow;
    }
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Alert

$m-alert-success-color: inherit !default;
$m-alert-error-color: #ea5353 !default;
$m-alert-success-bg-color: var(--m-neutral-color) !default;
$m-alert-error-bg-color: rgba(235, 83, 83, 0.3) !default;

.m-alert {
  &.alert-success,
  &.alert-danger {
    border: none;
  }

  &.alert-success {
    color: $m-alert-success-color;
    background-color: $m-alert-success-bg-color;
  }
  &.alert-danger {
    color: $m-alert-error-color;
    background-color: $m-alert-error-bg-color;
  }
}



////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Footer

.m-footer {

  --m-footer-bg-color: #333;
  --m-footer-text-color: #ccc;

  margin-top: $spacer-according-to-header;
  padding: .5rem 0;
  color: var(--m-footer-text-color);
  background-color: var(--m-footer-bg-color);
  transition: background-color $m-ani-change-mode-dur;
  transition-timing-function: $m-transition-timing-function;

  font-size: .8rem;

  //////////////////////////////////////////////////////////////////////////////
  /// DARK MODE

  .m-theme-dark & {
    --m-footer-bg-color: #fff;
    --m-footer-text-color: #333;
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// No Animation

@media screen and (prefers-reduced-motion: reduce){
  *,
  *:after,
  *:before {
    animation-duration: 0.001ms !important;
    animation-iteration-count: 1 !important;
    transition-duration:  0.001ms !important;
    transition-delay: 0 !important;
  }
}
