// dark-mode-toggle
// source: https://codepen.io/bnthor/pen/WQBNxO
//
// It's a creative toggle button for light & dark mode switch.
//
//
//
//
// @example
// <div class="ali-dark-mode-toggle">
//   <div class="ali-dark-mode-toggle__wrapper">
//     <input type="checkbox" id="dmt"/>
//     <label for="dmt" class="ali-dark-mode-toggle__label">
//       <span class="ali-dark-mode-toggle__handler">
//         <span class="dmt-crater dmt-crater--1"></span>
//         <span class="dmt-crater dmt-crater--2"></span>
//         <span class="dmt-crater dmt-crater--3"></span>
//       </span>
//       <span class="dmt-star dmt-star--1"></span>
//       <span class="dmt-star dmt-star--2"></span>
//       <span class="dmt-star dmt-star--3"></span>
//       <span class="dmt-star dmt-star--4"></span>
//       <span class="dmt-star dmt-star--5"></span>
//       <span class="dmt-star dmt-star--6"></span>
//     </label>
//   </div>
// </div>

.ali-dark-mode-toggle {
  --dark-mode-toggle-horizontal-space: 10px;
  --dark-mode-toggle-height: 22px;
  --dark-mode-toggle-vertical-scale: 22;

  display: inline-block;
  overflow: hidden;
  width: calc( ((var(--dark-mode-toggle-height) * 90) / 50) + 2px ); // 2px more to prevent cutting edges when the switch animation is running due to the hidden overflow
  height: var(--dark-mode-toggle-height);
  margin: 0 var(--dark-mode-toggle-horizontal-space);
}

.ali-dark-mode-toggle__wrapper {
  padding: 0;
  transform-origin: top left;
  transform: scale( calc( var(--dark-mode-toggle-vertical-scale) / 50 ) );

  input {
    // position: absolute;
    // left: -900em;
    display: none;
  }

  label {
    margin: 0;
  }
}

.ali-dark-mode-toggle__label {
  cursor: pointer;
  display: inline-block;
  position: relative;
  width: 90px;
  height: 50px;
  background-color: #83D8FF;
  border-radius: 90px - 6;
  transition: background-color 200ms cubic-bezier(0.445, 0.05, 0.55, 0.95);

  // &:before {
  //   content: 'Light';
  //   position: absolute;
  //   left: -52px;
  //   top: 10px;
  //   font-size: 18px;
  //   font-weight: bold;
  //   color: inherit;
  //   opacity: 1;
  // }
  //
  // &:after {
  //   content: 'Dark';
  //   position: absolute;
  //   right: -48px;
  //   top: 10px;
  //   font-size: 18px;
  //   font-weight: bold;
  //   color: inherit;
  //   opacity: .3;
  // }
}

.ali-dark-mode-toggle__handler {
  display: inline-block;
  position: relative;
  z-index: 1;
  top: 3px;
  left: 3px;
  width: 50px - 6;
  height: 50px - 6;
  background-color: #FFCF96;
  border-radius: 50px;
  box-shadow: 0 2px 6px rgba(0,0,0,.3);
  transition: all 400ms cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transform:  rotate(-45deg);

  .dmt-crater {
    position: absolute;
    background-color: #E8CDA5;
    opacity: 0;
    transition: opacity 200ms ease-in-out;
    border-radius: 100%;
  }

  .dmt-crater--1 {
    top: 18px;
    left: 10px;
    width: 4px;
    height: 4px;
  }

  .dmt-crater--2 {
    top: 28px;
    left: 22px;
    width: 6px;
    height: 6px;
  }

  .dmt-crater--3 {
    top: 10px;
    left: 25px;
    width: 8px;
    height: 8px;
  }
}

.dmt-star {
  position: absolute;
  background-color: #ffffff;
  transition: all 300ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
  border-radius: 50%;
}

.dmt-star--1 {
  top: 10px;
  left: 35px;
  z-index: 0;
  width: 30px;
  height: 3px;
}

.dmt-star--2 {
  top: 18px;
  left: 28px;
  z-index: 1;
  width: 30px;
  height: 3px;
}

.dmt-star--3 {
  top: 27px;
  left: 40px;
  z-index: 0;
  width: 30px;
  height: 3px;
}

.dmt-star--4,
.dmt-star--5,
.dmt-star--6 {
  opacity: 0;
  transition: all 300ms 0 cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.dmt-star--4 {
  top: 16px;
  left: 11px;
  z-index: 0;
  width: 2px;
  height: 2px;
  transform: translate3d(3px,0,0);
}

.dmt-star--5 {
  top: 32px;
  left: 17px;
  z-index: 0;
  width: 3px;
  height: 3px;
  transform: translate3d(3px,0,0);
}

.dmt-star--6 {
  top: 36px;
  left: 28px;
  z-index: 0;
  width: 2px;
  height: 2px;
  transform: translate3d(3px,0,0);
}

input:checked {
  + .ali-dark-mode-toggle__label {
    background-color: #749DD6;

    // &:before {
    //   opacity: .3;
    // }
    //
    // &:after {
    //   opacity: 1;
    // }

    .ali-dark-mode-toggle__handler {
      background-color: #FFE5B5;
      transform: translate3d(40px, 0, 0) rotate(0);

      .dmt-crater { opacity: 1; }
    }

    .dmt-star--1 {
      width: 2px;
      height: 2px;
    }

    .dmt-star--2 {
      width: 4px;
      height: 4px;
      transform: translate3d(-5px, 0, 0);
    }

    .dmt-star--3 {
      width: 2px;
      height: 2px;
      transform: translate3d(-7px, 0, 0);
    }

    .dmt-star--4,
    .dmt-star--5,
    .dmt-star--6 {
      opacity: 1;
      transform: translate3d(0,0,0);
    }
    .dmt-star--4 {
      transition: all 300ms 200ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
    }
    .dmt-star--5 {
      transition: all 300ms 300ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
    }
    .dmt-star--6 {
      transition: all 300ms 400ms cubic-bezier(0.445, 0.05, 0.55, 0.95);
    }
  }
}
